  }
}
.gridTV {
  display: grid;
  height: 100vh;
  width: 100vw;
  -ms-grid-template-columns: repeat(48, 1fr);
  -ms-grid-template-rows: repeat(24, 1fr);
  -ms-grid-column-gap: 0;
  -ms-grid-row-gap: 0;
  -o-grid-template-columns: repeat(48, 1fr);
  -o-grid-template-rows: repeat(24, 1fr);
  -o-grid-column-gap: 0;
  -o-grid-row-gap: 0;
  -webkit-grid-template-columns: repeat(48, 1fr);
  -webkit-grid-template-rows: repeat(24, 1fr);
  -webkit-grid-column-gap: 0;
  -webkit-grid-row-gap: 0;
  grid-template-columns: repeat(48, 1fr);
  grid-template-rows: repeat(24, 1fr);
  grid-column-gap: 0;
  grid-row-gap: 0;
}

